import React from 'react';
import {useEffect, useState} from 'react';
import {Card, Container, Grid, Image, Segment} from "semantic-ui-react";
import './ShowAdsCards.css'

function ShowAdsCards(props) {
    const [cardItems, setCardsItems ] = useState([]);
    useEffect(() => {
        fetch(`https://avoreshin.github.io/json-api/data-json.json`)
            .then(response => {
                if (!response.ok) {
                    throw new Error("HTTP error " + response.status);
                }
                return response.json();
            })
            .then(data => {
                setCardsItems(data.data);
            })
            .catch(error => console.error("data-json.json loader", error));
    }, []);



    return (
        <Container
        style={{marginTop:30}}>
            {cardItems.map(item => {
                return (
                    <Segment
                        // className={'Wrapper'}
                    >
                        <Grid
                        columns={3}
                        >
                            <Grid.Column>
                                <Image
                                    rounded
                                    className={'Wrapper__img'}
                                    verticalAlign='middle'
                                    src={item.photo[3].url}/>
                            </Grid.Column>
                            <Grid.Column>
                                <div>  {item.marka}</div>
                            </Grid.Column>
                            <Grid.Column>
                                <div>  {item.model}</div>
                            </Grid.Column>
                        </Grid>

                    </Segment>
                )
            })}

        </Container>
    );
}

export default ShowAdsCards;